$color_charade_approx: #282b34;
$color_mystic_approx: #e7ecef;
$black:  black;
$white: white;
$color_cadet_blue_approx: #a8b1bf;
$color_ivory_approx: #ffe;
$color_apple_approx: #4caf50;
$color_alto_approx: #ddd;
$font_0: Segoe UI;
.img-responsive {
  max-width: 300px;
}
#mainDiv {
  background-color: $color_charade_approx;
}
.content {
  background-color: $color_mystic_approx;
  opacity: 0.9;
  min-height: 920px;
  max-height: 1300px;
  border: 1px solid $black;
  border-top: 0;
}
ul {
  li a {
    color: $color_mystic_approx;
  }
  &.pagination li a {
    margin: 0 4px 4px;
    &.active {
      background-color: $color_apple_approx;
      color: $white;
    }
    &:hover:not(.active) {
      background-color: $color_alto_approx;
    }
  }
}
#registrationForm {
  width: 350px;
  height: 180px;
  background-color: $color_cadet_blue_approx;
  border-radius: 10px;
  margin-top: 150px;
}
#accountMenu {
  width: 350px;
  height: 180px;
  background-color: $color_cadet_blue_approx;
  border-radius: 10px;
  margin-top: 150px;
}
#newModal {
  z-index: 5;
  border: solid 2px $black;
  width: 500px;
  height: 500px;
  border-radius: 10px;
  top: 50%;
  left: 50%;
  display: none;
  margin-left: -350px;
  margin-top: -250px;
  opacity: 0;
  position: fixed;
  background: $color_ivory_approx;
}
#backs {
  z-index: 3;
  position: fixed;
  background-color: $black;
  opacity: 0.8;
  filter: alpha(opacity=80);
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  cursor: pointer;
  display: none;
}
#navBarL ul li {
  color: $white;
  width: 195px;
  height: 50px;
  padding: 0;
  border-left: 1px solid $black;
  border-right: 1px solid $black;
  font-family: $font_0;
  font-size: 20px;
  font-style: normal;
  font-variant: small-caps;
  font-weight: 400;
  line-height: 22px;
  text-align: center;
}